<template>
  <v-container>
    <PieChart :chartData="chartData" :options="options" v-if="loaded" />
  </v-container>
</template>

<script>
// @ is an alias to /src
import PieChart from "@/components/PieChart.vue";
import axios from "axios"
export default {
  components: {
    PieChart
  },
  data () {
    return {
      chartData: [],
      options: [],
      randomColor: [],
      key: [],
      value: [],
      target: this.$route.params.target,
      loaded: false
    }
  },
  created() {
    this.getContribution(this.target)
  },
  methods: {
    getContribution(target) {
      axios.get(`${this.$batchAPIUri}/job/contribution/${target}`)
        .then(res => { 
          this.loaded = true
                    
          for(let i=0; i<res.data.length; i++) {
            this.key[i] = res.data[i].key;
            this.value[i] = res.data[i].value;
            this.randomColor[i] = "#" + Math.floor(Math.random()*16777215).toString(16)
          }
          // chartData
          this.chartData = {
            labels: this.key,
            datasets: [
              {
                borderWidth: 5,
                borderColor: 'white',            
                backgroundColor: this.randomColor,
                data: this.value
              }
            ]
          },
          // options
          this.options = {
            onClick: function(point, event) {
              if(event.length <=0) return
              // let index = event[0]['_index']
              console.log(event[0]['_model']['label'])
            },
            legend: {
                display: true
            },
            responsive: true,
            maintainAspectRatio: false
          }      
        })
        .catch(err => {
          console.error(err)
        })
    },
  }
};
</script>